<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入标题" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="addItem" icon='plus'>新建文章</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="tittle" label="标题">
                    </el-table-column>
                    <el-table-column align="center" prop="articleContent" label="内容" :formatter="formatContent">
                    </el-table-column>
                    <el-table-column align="center" prop="articleType" label="状态" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column align="center" prop="browseTime" label="浏览次数" width="100">
                    </el-table-column>
                    <el-table-column align="center" prop="forwardTime" label="转发次数" width="100">
                    </el-table-column>
                    <el-table-column align="center" label="发表时间" width="120" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="230">
                        <template scope="scope">
                            <el-button size="small" type="info" @click="editItem(scope.$index, scope.row)" icon='edit'>编辑</el-button>
                            <el-button size="small" type="success" @click="copyLink(scope.$index, scope.row)">复制链接</el-button>
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2' title="删除"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        // 计算表格高度
        tableMaxHeight() {
            this.$store.commit('setTableMaxHeight', this.tableTop)
            return this.$store.state.tableMaxHeight
        }
    },
    methods: {
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.articleType == 1 ? '超值体验' : row.articleType == 2 ? '发现' : '未知'
            },
            formatContent(row, column) {
                return row.articleContent == ''? '暂无内容': row.articleContent
            },
        // 编辑文章
        editItem(index, row) {
            this.$store.commit('setArticleUuid', row.articleUuid)
            this.$router.push('/add-article')
        },
        // 新建文章
        addItem() {
            this.$store.commit('setArticleUuid', null)
            this.$router.push('/add-article')
        },
        // 复制链接
        copyLink(index, row) {
            if (this.$common.copyText(row.link)) {
                this.$message('复制成功!')
            } else {
                this.$message('暂无链接，未复制')
            }
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "articleUuid": row.articleUuid
                }
            }
            this.$common.deleteItem(this.API + 'article/delete', this, json)
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelArticle', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(tittle = ''){
            this.listLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "tittle": tittle,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('article/list', json, this, data => {
                this.total = data.total
                this.tableData = data.articleList
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.$store.commit('setArticleUuid', null)
        this.getList()
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



